@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,400;0,600;1,300&display=swap');
.mainHeader{
    background: rgba(31, 25, 31, 0.899);

}
#navbarSupportedContent {
    ul {
        li {
            transform: skewX(-15deg);
            padding: 30px 70px 30px 70px;

            a {
                color: #555455;
                transform: skewX(15deg);
                font-size: 20px;
                letter-spacing: 2%;
                line-height: 16px;
                font-family: 'Josefin Sans', sans-serif;


            }

            &:hover {
                background: linear-gradient(89.71deg, #BD0F71 0.22%, #7F028A 99.72%);

                a {
                    color: rgb(255, 255, 255);

                    &::after {
                        content: "";
                        align-items: center;
                        text-align: center;
                        display: block;
                        width: 100%;
                        height: 1px;
                        background: #ffffff;
                        right: 0;
                        left: 0;
                    }
                }
            }
        }
    }
}